import axios from 'axios'
import { post, get } from './config'

export default {
  /*
  * 
  * 
  */
  upImg (e) {
    var self = this
    let file = e.target.files[0]
    let param = new FormData() // form
    param.append('file', file, file.name) // appendform
    param.append('chunk', '0') // form
    console.log(param.get('file')) // FormDataget
    // console.log(param)
    return axios.post(
      'api/login/upload',
      param,
      {headers: {'Content-Type': 'multipart/form-data'}}
    ).then((res) => {
      return Promise.resolve(res.data)
    })
  },

  // 
  getBusinessInfo () {
    return get('api/Home/company_info')
  },
  // 
  getCode (data){
    return post('api/login/sendSMS',data)
  },
  // 
  loginFun (data) {
    return post('api/login/message_login', data)
  },
  // 
  getUserInfo (data) {
    return post('api/user/user_info', data)
  },
  // 
  fankui (data) {
    return post('api/User/feedback', data)
  },
  // 
  applyseller (data) {
    return post('api/Merchant/sub_material', data)
  },
  // 
  applyinfo (data) {
    return post('api/Merchant/material_info', data)
  },
  // html
  gethtmlInfo (data) {
    return get('api/Merchant/ajax_agreement/id/'+data)
  },
}






