<template>
  <div class="pageBox">
    <my-header></my-header>
    <header-content :left-type.sync="leftType"></header-content>
    <div class="container mePageBox">
      <div class=" clearfix">
        <div class="left-box">
          <clone-left :left-type="leftType"></clone-left>
        </div>
        <div class="right-box">
          <navigation>
            <router-view />
          </navigation>
        </div>
      </div>
    </div>
    <my-footer></my-footer>
  </div>
</template>

<script>
  import MyHeader from '@/components/header/header'
  import MyFooter from '@/components/footer/footer'

  import cloneLeft from './clonebox/cloneLeft'
  import HeaderContent from './clonebox/headerContent'
  export default {
    data () {
      return {
        leftType: this.$route.query.leftype
      }
    },
    beforeRouteUpdate (to, from, next) {
      if(to.path == '/clone/help'){
        this.leftType = 2
      }else if(to.path == '/clone/fankui'){
        this.leftType = 1
      }else {
        this.leftType = 3
      }
      next()
    },
    components: {
      MyHeader,
      MyFooter,
      HeaderContent,
      cloneLeft
    }
  }
</script>

<style scoped>
  .mePageBox{
    padding: 20px 0;
    min-height: 600px;
  }
  .left-box{
    float: left;
    width: 220px;
    height: 530px;
  }
  .right-box{
    float: right;
    width: 960px;
  }
</style>



