<template>
  <div class="pageBox">
    <my-header></my-header>
    <div class="headerContent clearfix">
      <div class="box-left clearfix">
        <router-link to="/" class="logoImg">
          <img src="../../assets/images/corelogo.png" alt="">
        </router-link>
      </div>
    </div>
    <div class="inner p15">
      <h1 class="f24 box_center f_co_3F8FDF"></h1>
      <div class="mt-30 moz bor p30">
        <!--  -->
        <div class="box mt-10">
          <div class="ddlist-box ddxq-spxx bg-fff" style="padding:0px 30px 30px 30px;">
            <div class="product-info"></div>
            <div class="ddlist-nav">
              <ul>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <div class="table " style="margin: 10px 0px 30px;">
              <table cellspacing="0" cellpadding="0">
                <tbody><tr class="">
                  <td class="td1">
                    <div class="dd-l-list clearfix">
                      <ul>
                        <li class="img"><router-link :to="{path: '/mall/goodsDetails', query: {goods_id: OrderInfo.goods_id}}" ><img :src="OrderInfo.goods_img"></router-link></li>
                        <li class="txt"><a href="javascript:;" v-text="OrderInfo.goods_name"></a></li>
                      </ul>
                    </div>
                  </td>
                  <td class="td2"><span style="text-align:center;" >&yen;<em v-text="OrderInfo.specification_price"></em></span></td>
                  <td class="td2"><span style="text-align:center;" ><em v-text="OrderInfo.goods_num"></em></span></td>
                  <td class="td2"><span style="text-align:center;" >&yen;<em v-text="OrderInfo.specification_price*OrderInfo.goods_num"></em></span></td>
                </tr>
                </tbody></table>
            </div>
            <div class="d-s-money" style="background:#F8F8F8">
              <ul>
                <li><em v-text="OrderInfo.totalNum"></em>&yen;<span v-text="OrderInfo.specification_price*OrderInfo.goods_num"></span></li>
                <!-- <li><span >&yen;<span v-text="0"></span></span></li> -->
                <!-- <li><span >-&yen;<span v-text="OrderInfo.deduct_integral_value"></span></span></li> -->
                <!-- <li><span >-&yen;<span v-text="OrderInfo.deduct_integral_value"></span></span></li> -->
                <li><span class="red ">&yen;<span class="red" v-text="OrderInfo.refund_price*OrderInfo.goods_num"></span></span></li>
              </ul>
            </div>
          </div>
        </div>
        <div class="box_center">
          <div class="mr-10">
            <div class="box_center h30"><span class="col_red"></span></div>
            <div class="box_center h-40 pl10 pr10 bor_b">
              <select class="h-36 w-300" v-model="refund_type" name="" >
                <option value="1">()</option>
                <option value="2">()</option>
              </select>
            </div>
          </div>
          <div>
            <div class="box_center h30"><span class="col_red"></span></div>
            <div class="box_center h-40 pl10 pr10 bor_b">
              <select class="h-36 w-300" v-model="reason_name" name="" >
                <option :value="item.reason_name" v-for="item in refundreasons" v-text="item.reason_name"></option>
              </select>
            </div>
          </div>
        </div>

        <div class="box_end mt-30 f16" style="padding-right: 150px;">
          <div class="box_start h30 "> : &nbsp;</div>
          <div class=" br3 box_start h40 pl10 pr10  red f26"> &nbsp; <em  v-text="OrderInfo.refund_price*OrderInfo.goods_num"></em></div>
        </div>

        <div class="mt-30">
          <h3 class=" h30 t_c"></h3>
          <div class="back_fff br3 box_center pl10 pr10 bor moz" style="width: 600px;height: 300px;">
            <textarea v-model="refund_desc" class="w100 h100 p15 pt5 pb5 f14" type="text" placeholder=""></textarea>
          </div>
        </div>

          <div class="main-item">
            <button class="addressBtn" @click="add_apply"></button>
          </div>
        </div>
      </div>

    <my-footer></my-footer>
    </div>
</template>

<script>
  import MyHeader from '@/components/header/header'
  import MyFooter from '@/components/footer/footer'
  import api from '@/api/order'
  import me from '@/api/me'
  import storage from '@/common/storage'
  export default {
    data () {
      return {
        order_goods_id: this.$route.query.order_goods_id,
        order_merchants_id: this.$route.query.order_merchants_id,
        refund_type:'',reason_name:'',refund_desc:'',
        OrderInfo:{},refundreasons:[]
      }
    },
    methods:{

      add_apply(){
        if (!this.refund_type) {
          this.promptFun({content: '', type: 'error'})
          return false;
        }else if(!this.reason_name) {
          this.promptFun({content: '', type: 'error'})
          return false;
        }
        api.refundapply({
          uid: this.getCookie('uid'),
          token: this.getCookie('token'),
          order_goods_id: this.order_goods_id,
          order_merchants_id: this.order_merchants_id,
          refund_count: this.OrderInfo.goods_num,
          refund_type: this.refund_type,
          refund_reason: this.reason_name,
          refund_desc: this.refund_desc
        })
          .then((res) => {
            if(res.status === 'ok'){
              this.promptFun({
                content: '',
                type: 'success'
              })
            }else if(res.status === 'error'){
              this.promptFun({
                content: res.data,
                type: 'error'
              })
            }
          })
      },
// 
      _getrefundreason(){
        api.getrefundreason({
        }).then((res) => {
          console.log(res)
          if(res.status === 'ok'){
            this.refundreasons = res.data;
          }else if(res.status === 'error'){
            this.promptFun({
              content: res.data,
              type:"error"
            })
          }
        })
      },
      // 
      _getOrderInfo () {
        api.getRefundGoodsDetail({
          uid: this.getCookie('uid'),
          token: this.getCookie('token'),
          order_merchants_id:parseInt(this.$route.query.order_merchants_id),
          order_goods_id:parseInt(this.$route.query.order_goods_id)
        }).then((res) => {
          console.log(res)
          if(res.status === 'ok'){
            this.OrderInfo = res.data;
          }else if(res.status === 'error'){
            this.promptFun({
              content: res.data,
              type:"error"
            })
          }
        })
      }
    },
    created () {
      this._getOrderInfo();
      this._getrefundreason();
    },
    components: {
      MyHeader,
      MyFooter
    }
  }
</script>

<style scoped>
  .inner{margin: 0 auto;width: 1200px;}.width120{width: 120px;}
  .p30{padding: 30px;}.itemBox{  height: 26px;  line-height: 26px;  }.f26{font-size: 26px;}.col_red{color: red}
  .h30{height: 30px;}.w140{width: 140px;}.h140{height: 140px;}.mb-15{margin-bottom: 15px;}
  .main-item{
    position: relative;
    margin-top: 50px;
    padding-bottom: 10px;
    zoom: 1;
    text-align: center;
    font-size: 12px;
  }
  .addressBtn{
    width: 100px;
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
    border-radius: 5px;
    color: #fff;
    background-color: #fc6210;
    text-align: center;
    font-weight: 700;
    cursor: pointer;
  }
  .headerContent{
    width: 1200px;
    margin: 0 auto;
    padding: 20px 0;
  }
  .box-left{
    float: left;
  }
  .logoImg{
    width: 120px;
    height: 70px;
    display: block;
    float: left;
  }
  .shoppingCar{
    display: block;
    height: 38px;
    line-height: 38px;
    border: 1px solid #f1f1f1;
    font-size: 14px;
    margin: 15px 0;
    padding: 0 20px;
    float: right;
  }
  .box{
    background-color: #fff;
    padding: 30px;
  }
  .btn {
    display: block;
    padding: 5px 15px;
    border-radius: 5px;
    color: #fff;
    margin-top: 50px;
    background-color:#3f8fdf;
    /*border: 1px solid #3f8fdf;*/
    text-align: center;
  }
  .product-info {

    height: 50px;
    line-height: 50px;
    font-size: 20px;
    color: #333333;
  }
  .ddlist-nav {
    height: 50px;
    line-height: 50px;
    background: #f1f2f6;
  }
  .ddlist-nav ul li:nth-child(1) {
    width: 510px;
  }
  .ddlist-nav ul li:nth-child(2) {
    width: 145px;
  }
  .ddlist-nav ul li:nth-child(3) {
    width: 145px;
  }
  .ddlist-nav ul li:nth-child(4) {
    width: 145px;
  }
  .ddlist-nav ul li {
    text-align: center;
    float: left;
  }
  .table table {
    border-collapse: collapse;
    width: 100%;
  }
  .ddlist-box tr {
    border: 1px solid #ebebeb;
  }
  .ddlist-box .td1 {
    width: 436px;
  }
  .ddlist-box td {
    border: 1px solid #ebebeb;
    padding: 30px 20px;
  }
  .dd-l-list:last-child {
    margin-bottom: 0px!important;
  }
  .dd-l-list:first-child {
    border: none;
  }
  .dd-l-list {
    margin-bottom: 0px;
    border-top: 1px solid #ebebeb;
  }
  .ddlist-box .td2 {
    color: #999;width: 145px;
  }
  .dd-l-list .img {
    width: 80px;
    height: 80px;
    overflow: hidden;
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
  .dd-l-list li {
    display: inline-block;
    float: left;
    line-height: 80px;
  }
  .dd-l-list .txt {
    width: 190px;
    padding-left: 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .dd-l-list .img img {
    width: 80px;
  }
  .ddlist-box .td2 span {
    display: block;
    color: #999;
  }
  .d-s-money {
    text-align: right;
    padding: 30px;
  }
  .d-s-money li {
    margin-bottom: 20px;
    color: #666;
  }
  .d-s-money span {
    display: inline-block;
    min-width: 120px;
    text-align: left;
    padding-left: 20px;
    color: #666;
  }
  .blue {
    color: #2173c5!important;
  }
  .d-s-money li:last-child {
    font-size: 24px!important;
    color: #333;
  }
  .red {
    color: #fd655a!important;
  }
  .el-step__icon{
    background: #e3e4e5!important;
  }
  .el-step__head.is-finish {
    color: #fd655a;
    border-color: #fd655a;
  }
</style>



