import { post, get } from './config'

export default {
	// 
	orderPay (type, data) {
		let api = ''
		if(type == 1){ // 
			api = 'api/Pingxx/ping1'
		}else { // 
			api = 'api/Pingxx/ping2'
		}
		return post(api, data)
	},
	// 
	getOrderPayDetails (type, data) {
		let api = ''
		if(type == 1){  // 
			api = 'api/Order/getOrderView'

		}else { // 
			api = 'api/Order/getMerchantOrderView'
		}
		return post(api, data)
	},
	// 
	getOrderList (data) {
		return post('api/Order/queryOrderByState', data)
	},
	// 
	getOrderDetails (data) {
		return post('api/Order/queryOrderView', data)
	},
	// 
	getLogDetails (data) {
		return post('api/Express/getTracesByJson', data)
	},
	// 
	cancelOrder (data) {
		return post('api/Order/cancelOrder', data)
	},
	// 
	receiveOrder (data) {
		return post('api/Order/receiveOrder', data)
	},
	// 
	deleteOrder (data) {
		return post('api/Order/delOrder', data)
	},
  // 
  getRefundList (data) {
    return post('api/Order/refund_order', data)
  },
  // 
  getRefundDetail (data) {
    return post('api/Order/refund_order_view', data)
  },
  // 
  getRefundGoodsDetail (data) {
    return post('api/Order/refund_goods', data)
  },
  // 
  getrefundreason (data) {
    return post('api/Order/order_refund_reason', data)
  },
  // 
  refundapply (data) {
    return post('/api/Order/apply_refund', data)
  }
}



