<template>
  <div class="pageBox">
    <my-header></my-header>
    <div class="headerContent clearfix">
      <div class="box-left clearfix">
        <router-link to="/" class="logoImg">
          <img src="../../assets/images/corelogo.png" alt="">
        </router-link>
      </div>
    </div>
    <div class="inner p15">
      <h1 class="f24 box_center f_co_3F8FDF"></h1>
      <div class="mt-30 w55 moz bor p30">
        <!--  -->
        <div v-show="applyInfo.apply_state != 'refuse'" class="back_fff mt10">
          <div class="box_center">
            <div class="w_b80 p_r back_999 mt10 mb10 box_between " :class="{apply_state2:isactive,apply_state3:!isactive}">
            </div>
          </div>
          <div class="box_center pb10">
            <div class="w_b85 box_between f12">
              <div></div>
              <div></div>
              <div></div>
            </div>
          </div>
        </div>
        <!--  -->
        <div v-show="applyInfo.apply_state == '3' " class="back_fff mt10 box_start p10 f16 col_red">
          
        </div>
        <!---->
        <!--<div class="back_fff h-50 box_between pl-10 pr-10 mt10 mb10 " v-if="applyInfo.apply_state == '2' && applyInfo.pay_state == '0'">-->
          <!--<div> <span class="col_red">&yen;</span><span class="col_red" v-text="applyInfo.deposit"></span></div>-->
          <!--<div class="addressBtn " @click="payClick"></div>-->
        <!--</div>-->


        <div class="pl-10 pr-10 box_start f14 h-40">
          <div></div>
        </div>
        <div class="back_fff ">
          <div class="pl-10 pr-10 box_between bor-b f12 h-40">
            <div></div>
            <div class="f_co_999" v-text="applyInfo.contact_name"></div>
          </div>
          <div class="pl-10 pr-10 box_between bor-b f12 h-40">
            <div></div>
            <div class="f_co_999" v-text="applyInfo.contact_mobile"></div>
          </div>
          <div class="pl-10 pr-10 box_between bor-b f12 h-40">
            <div></div>
            <div class="f_co_999" v-text="applyInfo.business_number"></div>
          </div>
          <div class="pl-10 pr-10 box_between bor-b f12 h-40">
            <div></div>
            <div class="f_co_999" v-text="applyInfo.merchants_name"></div>
          </div>
          <div class="pl-10 pr-10 box_between bor-b f12 h-40">
            <div></div>
            <div class="f_co_999" v-text="applyInfo.merchants_address"></div>
          </div>
        </div>

        <div class="back_fff mt10">
          <div class="h-40 box_start f12 pl-10"></div>
          <div class="box_between pb10" v-if="applyInfo.legal_img">
            <div class="w25 box_center">
              <div class="w-70 h-70">
                <img :src="applyInfo.legal_img" alt="">
              </div>
            </div>
            <div class="w25 h-70 box_center">
              <div class="w-70 h-70">
                <img :src="applyInfo.legal_hand_img" alt="">
              </div>
            </div>
            <div class="w25 h-70 box_center">
              <div class="w-70 h-70">
                <img :src="applyInfo.legal_face_img" alt="">
              </div>
            </div>
            <div class="w25 h-70 box_center">
              <div class="w-70 h-70">
                <img :src="applyInfo.legal_opposite_img" alt="">
              </div>
            </div>
          </div>
          <div v-if="applyInfo.business_img" class="h-40 box_start f12 pl-10"></div>
          <div v-if="applyInfo.business_img" class="box_start pb10">
            <div class="w25 box_center">
              <div class="w-70 h-70">
                <img :src="applyInfo.business_img" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
    <my-footer></my-footer>
  </div>
</template>

<script>
  import MyHeader from '@/components/header/header'
  import MyFooter from '@/components/footer/footer'
  import api from '@/api/api'
  import storage from '@/common/storage'
  export default {
    data () {
      return {
        isactive:false,
        leftType: this.$route.query.apply_state,
        upimgarr:[],upimgarr0:'',upimgarr1:'',upimgarr2:'',upimgarr3:'',upimgarr4:'',
        contact_name:'',contact_mobile:'',merchants_name:'',merchants_address:'',business_number:'',
        address_province: '', address_city: '', address_country: '',
        applyInfo:'',
        uid: this.getCookie('uid') || '',
        token: this.getCookie('token') || '',
        userInfo: this.getCookie('userInfo') || {},
      }
    },
    methods:{
      // 
      applyinfo(){
        api.applyinfo({
          uid: this.getCookie('uid'),
          token: this.getCookie('token')
        })
          .then((res) => {
            if(res.status === 'ok'){
              this.applyInfo = res.data
              if(res.data.apply_state=='1'){this.isactive=true}
//              this.promptFun({
//                content: '',
//                type: 'success'
//              })
            }else if(res.status === 'error'){
              this.promptFun({
                content: res.data,
                type: 'error'
              })
            }
          })
      },
      payClick(){
        console.log("")

      }


    },
    created(){
      this.applyinfo()
    },
    components: {
      MyHeader,
      MyFooter,

    }
  }
</script>

<style scoped>
  .inner{margin: 0 auto;width: 1200px;}.width120{width: 120px;}.pb10{padding-bottom: 10px;}
  .p30{padding: 30px;}.itemBox{  height: 26px;  line-height: 26px;  }.w_b85{width: 85%;}.br15{border-radius: 15px;}
  .h30{height: 30px;}.w140{width: 140px;}.h140{height: 140px;}.mb-15{margin-bottom: 15px;}.col_red{color: red}
  .back_fff{background: #fff;}.mt10{margin-top: 10px;}.w_b80{width: 80%;}.back_999{background: #999;}.mb10{margin-bottom: 10px;}

  .main-item{  position: relative;  margin-top: 50px;  padding-bottom: 10px;  zoom: 1;  text-align: center;  font-size: 12px;  }
  .addressBtn{
    width: 100px;
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
    border-radius: 5px;
    color: #fff;
    background-color: #fc6210;
    text-align: center;
    font-weight: 700;
    cursor: pointer;
  }
  .headerContent{
    width: 1200px;
    margin: 0 auto;
    padding: 20px 0;
  }
  .box-left{
    float: left;
  }
  .logoImg{
    width: 120px;
    height: 70px;
    display: block;
    float: left;
  }
  .shoppingCar{
    display: block;
    height: 38px;
    line-height: 38px;
    border: 1px solid #f1f1f1;
    font-size: 14px;
    margin: 15px 0;
    padding: 0 20px;
    float: right;
  }
  .apply_state2{background:url(../../assets/icon/ruzhushenhe_dangqian2@2x.png) no-repeat;background-size: cover;background-position: center;height: 21px;}
  .apply_state3{background:url(../../assets/icon/ruzhushenhe_dangqian3@2x.png) no-repeat;background-size: cover;background-position: center;height: 21px;}

</style>



