import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/login/login'

import Mall from '@/pages/mall/mall'
import Home from '@/pages/mall/home/home'
import GoodsClass from '@/pages/mall/goodsClass/goodsClass'
import ShopDetails from '@/pages/mall/shopDetails/shopDetails'
import GoodsDetails from '@/pages/mall/goodsDetails/goodsDetails'
import ConfirmOrder from '@/pages/mall/confirmOrder/confirmOrder'
import OrderPay from '@/pages/mall/orderPay/orderPay'
import searchbox from '@/pages/mall/searchbox/searchbox'

import ShoppingCar from '@/pages/mall/shoppingCar/shoppingCar'

import Portal from '@/pages/portal/portal'
import MeDetails from '@/pages/portal/meDetails/meDetails'
import EditUser from '@/pages/portal/editUser/editUser'

import OrderList from '@/pages/portal/order/orderList/orderList'
import RefundOrder from '@/pages/portal/order/refundOrder/refundOrder'
import EvaluateList from '@/pages/portal/order/evaluateList/evaluateList'
import orderDetail from '@/pages/portal/order/orderDetail/orderDetail'

import GoodsCollection from '@/pages/portal/collection/goodsCollection/goodsCollection'
import MyFollow from '@/pages/portal/collection/myFollow/myFollow'

import MyAddress from '@/pages/portal/myAddress/myAddress'
import becomeSeller from '@/pages/becomeSeller/becomeSeller'
import sellerProcess from '@/pages/becomeSeller/sellerProcess'
import applyRefund from '@/pages/becomeSeller/applyRefund'
import refundetail from '@/pages/becomeSeller/refundetail'
import outforid from '@/pages/becomeSeller/outforid'
import clone from '@/pages/clone/clone'
import AboutUs from '@/pages/clone/aboutUs/aboutUs'
import fankui from '@/pages/clone/aboutUs/fankui'
import help from '@/pages/clone/help/help'

Vue.use(Router)

export default new Router({

  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: "/mall/home", // 
      component: Mall
    },{
      path: '/login',
      name: 'login',
      component: Login
    },{
      path: '/mall',
      name: 'mall',
      redirect: "/mall/home", // 
      component: Mall,
      children: [
        {
          path: 'home', // 
          name: 'home',
          component: Home
        },{
          path: 'goodsClass', // 
          name: 'goodsClass',
          component: GoodsClass,
          props: true
        },{
          path: 'goodsDetails', // 
          name: 'goodsDetails',
          component: GoodsDetails
        },{
          path: 'shopDetails', // 
          name: 'shopDetails',
          component: ShopDetails
        },{
          path: 'searchbox', // 
          name: 'searchbox',
          component: searchbox
        }
      ]
    },{
      path: '/confirmOrder', // 
      name: 'confirmOrder',
      component: ConfirmOrder
    },{
      path: '/orderPay', // 
      name: 'orderPay',
      component: OrderPay
    },{
      path: '/shoppingCar', // 
      name: 'shoppingCar',
      component: ShoppingCar
    },{
      path: '/portal',
      name: 'portal',
      redirect: "/portal/meDetails", // 
      component: Portal,
      children: [
        {
          path: 'meDetails', //
          name: 'meDetails',
          component: MeDetails
        },{
          path: 'editUser', // 
          name: 'editUser',
          component: EditUser
        },{
          path: 'orderList', // 
          name: 'orderLisr',
          component: OrderList
        },{
          path: 'refundOrder', // 
          name: 'refundOrder',
          component: RefundOrder
        },{
          path: 'evaluateList', // 
          name: 'evaluateList',
          component: EvaluateList
        },{
          path: 'goodsCollection', // 
          name: 'goodsCollection',
          component: GoodsCollection
        },{
          path: 'myFollow', // 
          name: 'myFollow',
          component: MyFollow
        },{
          path: 'myAddress', // 
          name: 'myAddress',
          component: MyAddress
        },{
          path: 'orderDetail', // 
          name: 'orderDetail',
          component: orderDetail
        }
      ]
    },
    {
      path: '/clone',
      name: 'clone',
      redirect: "/clone/aboutUs", // 
      component: clone,
      children:[
        {
          path: 'aboutUs', // 
          name: 'aboutUs',
          component: AboutUs
        },
        {
          path: 'fankui', // 
          name: 'fankui',
          component: fankui
        },
        {
          path: 'help', // 
          name: 'help',
          component: help
        }
      ]
    },
    {
      path:'/becomeSeller',// 
      name: 'becomeSeller',
      component: becomeSeller
    },
    {
      path:'/sellerProcess',// 
      name: 'sellerProcess',
      component: sellerProcess
    },{
      path: '/applyRefund', // 
      name: 'applyRefund',
      component: applyRefund
    },{
      path: '/refundetail', // 
      name: 'refundetail',
      component: refundetail
    },{
      path: '/outforid', // 
      name: 'outforid',
      component: outforid
    }
  ]
})



