<template>
  <div class="box">
    <div v-html="htmlInfo"></div>
  </div>
</template>

<script>
  import '@/assets/css/cuimeng_style.css'
  import api from '@/api/api'
  import storage from '@/common/storage'

  export default {
    data () {
      return {
        htmlInfo:'',
        htmlid:this.$route.query.id || 6
      }
    },
    methods: {
      // 
      _gethtmlInfo () {
        api.gethtmlInfo(this.htmlid).then((res) => {
          console.log(res)
          if(res.status === 'ok'){
            this.htmlInfo = res.data;
          }else if(res.status === 'error'){
            this.promptFun({
              content: res.data
            })
          }
        })
      }


    },
    created () {
      this._gethtmlInfo();
    },
    components: {

    }
  }
</script>

<style scoped>
  .box{
    background-color: #fff;
    padding: 30px;
    padding-bottom: 50px;
  }
  .userInfoBox{
    padding: 20px 0;
  }
  .userImg{
    width: 120px;
    height: 120px;

  }
  .userImg img{
    border-radius: 50%;
    border: 1px solid #e3e4e5;
  }
  .userTxt{
    padding-top: 10px;
    margin-left: 40px;
    width: 300px;
  }
  .btn_upimg{
    font-size: 16px;
    border:1px solid #e3e4e5;
    border-radius: 10px;
    width: 150px;
    height: 40px;
    margin-top: 40px;
    margin-left: 30px;
    background-color: #f1f1f1;
    position: relative;
  }
  .btn_upinfo{
    font-size: 16px;
    width: 150px;
    height: 30px;
    color: #fff;
    background-color: #3f8fdf;
  }
  .birthdayselect select {
    margin-right: 6px;
    height: 26px;
    padding: 0px 6px;
    border: 1px solid #ebebeb;
    color: #666;
  }
  .userFile{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
</style>



