<template>
  <div>
          <div class="box">
       <div class="box_start ">
         <div class="f20"></div>
       </div>
       <!--<div class="mt-30">-->
         <!--<div class="dd-nav">-->
           <!--<a href="JavaScript:;"  class="" v-bind:class=" refund_state===''?'act':''" v-on:click="_getRefundList('',1)"></a>-->
           <!--<a href="JavaScript:;"  class="" v-bind:class=" refund_state==='wait_review'?'act':''" v-on:click="_getRefundList('wait_review',1)"></a>-->
           <!--<a href="JavaScript:;"  class="" v-bind:class=" refund_state==='accept'?'act':''" v-on:click="_getRefundList('accept',1)"></a>-->
           <!--<a href="JavaScript:;"  class="" v-bind:class=" refund_state==='refuse'?'act':''" v-on:click="_getRefundList('refuse',1)"></a>-->
           <!--<a href="JavaScript:;"  class="" v-bind:class=" refund_state==='end'?'act':''" v-on:click="_getRefundList('end',1)"></a>-->

         <!--</div>-->
       <!--</div>-->
       <div class="ddlist-nav2 mt-60 mb-30">
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <div class="ddlist-box">
        <div class="w100 ddlist-ref ">
          <ul v-for="item in orderList ">
          <li v-text="item.refund_no"></li>
          <li v-text="item.goods_name"></li>
          <li class="wbyc2" v-text="item.merchants_name"></li>
          <li>&yen;<span v-text="item.specification_price * item.refund_count"></span></li>
          <li>&yen;<span v-text="item.refund_price"></span></li>
          <li v-text="item.refund_state=='wait_review' ? '' : item.refund_state=='accept' ? '' : item.refund_state== 'refuse'? '' : item.refund_state=='end' ? '' : ''"></li>
          <li><router-link :to="{path:'/refundetail',query:{refund_id:item.refund_id}}"></router-link></li>
        </ul>
        </div>
        <div class="paginaction mt-30" v-show="allPage >= 1">
          <el-pagination
            :prev-text="''"
            :next-text="''"
            background
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="4"
            layout="total,prev, pager, next, jumper"
            :page-count="allPage">
          </el-pagination>
        </div>
      </div>



    </div>
  </div>
</template>

<script>
  import { Pagination } from 'element-ui'
  import storage from '@/common/storage'
  import api from '@/api/order'
import '@/assets/css/cuimeng_style.css'
export default {
  data () {
    return {
      orderList : [],
      currentPage: 1, // 
      allPage: 1 ,// 
      sortType: 1, // 1()
      refund_state:''
    }
  },
  methods: {
// 
    _getRefundList (type,p) {
      this.orderList = []
      this.currentPage = p || 1
      this.refund_state = type || ''
      api.getRefundList({
        uid: this.getCookie('uid'),
        token: this.getCookie('token'),
//        refund_type : this.refund_state,
        p : this.currentPage,
        pagesize: 4
      }).then(res => {
        console.log(res)
        if(res.status === 'ok'){
          this.allPage = res.data.page
          for(let i=0; i<res.data.list.length; i++){
            this.orderList.push(res.data.list[i])
          }
        }else if(res.status === 'error'){
          this.promptFun({
            content: res.data,
            type: 'error'
          })
        }
      })
    },
    //onPage
    onPage (count) {
      this.currentPage = count
    },
    // 
    handleCurrentChange (val) {
      this._getRefundList(this.refund_state , val)
    },
  },
  created () {
    this._getRefundList(this.refund_state )
  },
  components: {
    ElPagination: Pagination,
  }
}
</script>

<style scoped>
 .box{
    background-color: #fff;
    padding: 30px;
  }
 .act {
   border-bottom: 2px solid #3f8fdf;
   color: #3f8fdf;
 }
  dd-nav>a.act, .dd-nav>a:hover {
    border-bottom: 2px solid #3f8fdf;
    color: #3f8fdf;
}
.dd-nav>a {
    display: inline-block;
    border-bottom: 2px solid #fff;
    margin-right: 10px;
    line-height: 38px;
}
.dd-nav>div {
    float: right;
    display: inline-block;
    width: 200px;
    height: 40px;
    border: 1px solid #ebebeb;
    background: #f1f1f1;
    line-height: 38px;
}
.dd-nav>div input {
    border: 0px;
    padding-left: 7px;
    height: 38px;
    line-height: 38px;
    width: 140px;
    background: #f1f1f1;
    float: left;
}
input {
    outline: none;
    border-radius: 0px;
    font-family: tahoma,arial,Microsoft YaHei,Hiragino Sans GB,"\u5b8b\u4f53",sans-serif;
    font-size: 14px;
}
.dd-nav>div i {
    width: 58px;
    border: 0px;
    height: 38px;
    text-align: center;
    color: #fff;
}
.ddlist-nav {
    width: 100%;
    height: 50px;
    line-height: 50px;
    background: #f1f2f6;
}
.ddlist-nav2 {
    height: 50px;
    line-height: 50px;
    background: #f1f2f6;
}
.ddlist-nav2 ul li:nth-child(1) {
    width: 180px;
}.ddlist-nav2 ul li:nth-child(2) {
    width: 196px;
}.ddlist-nav2 ul li:nth-child(3) {
    width: 140px;
}.ddlist-nav2 ul li:nth-child(4) {
    width: 90px;
}.ddlist-nav2 ul li:nth-child(5) {
    width: 90px;
}.ddlist-nav2 ul li:nth-child(6) {
    width: 90px;
}.ddlist-nav2 ul li:nth-child(7) {
    width: 90px;
}
.ddlist-nav2 ul li {
    text-align: center;
    float: left;
}
.ddlist-ref{
  height: 130px;
  padding-top: 40px;
  padding-bottom: 40px;
  /*background: #f1f2f6;*/
  border-bottom: 1px solid #ebebeb;
  position: relative;
}
.ddlist-ref:last-child{
  border:none;
}
.ddlist-ref ul{}
.ddlist-ref ul li:nth-child(1) {
    width: 180px;
}.ddlist-ref ul li:nth-child(2) {
    width: 196px;
}.ddlist-ref ul li:nth-child(3) {
    width: 140px;
    color: #999;
}.ddlist-ref ul li:nth-child(4) {
    width: 90px;
       color: #999;
}.ddlist-ref ul li:nth-child(5) {
    width: 90px;
        color: #999;
}.ddlist-ref ul li:nth-child(6) {
    width: 90px;
        color: #ff9e31;
}.ddlist-ref ul li:nth-child(7) {
    width: 90px;
    color: #2173c5;
}
.ddlist-ref ul li {
    text-align: center;
    line-height: 25px;
    float: left;
}




.ddlist-nav ul li:nth-child(1) {
    width: 441px;
}
.ddlist-nav ul li {
    text-align: center;
    float: left;
    width: 145px;
}
.dd-l-list .sqsh{width:80px;text-align:center}
.ddlist {
    margin-bottom: 40px;
}
.ddlist>.tit {
    height: 36px;
    line-height: 36px;
    background: #fbfbfb;
}
.ddlist>.tit span {
    margin-left: 30px;
    font-size: 12px;
    color: #666;
}
.ddlist>.tit span img {
    width: 14px;
    height: 14px;
    margin-left: 5px;
}
.table table {
    border-collapse: collapse;
    width: 100%;
}
.ddlist-box tr {
    border: 1px solid #ebebeb;
}
.ddlist-box .td1 {
    width: 436px;
}
.ddlist-box td {
    border: 1px solid #ebebeb;
    padding: 30px 20px;
}
.ddlist-box .td2 {
    width: 145px;
}


.dd-l-list:last-child {
    margin-bottom: 0px!important;
}
.dd-l-list:first-child {
    border: none;
}
.dd-l-list {
    margin-bottom: 0px;
    border-top: 1px solid #ebebeb;
}
.dd-l-list .img {
    width: 80px;
    height: 80px;
    overflow: hidden;
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}
.dd-l-list .img img {
