<template>
  <div class="box">
    <div class="fk-center">
      <p class="tit2"><span class="blue">52</span></p>
      <div class="w70 mt-20">
        <textarea v-model="message" rows="10" cols="30" class="bor moz w100 p15" style="resize:auto;text-indent: 10px; ;" placeholder=''></textarea>
      </div>
      <button v-on:click="yjfkbtn()"></button>
    </div>
  </div>
</template>

<script>
  import '@/assets/css/cuimeng_style.css'
  import api from '@/api/api'
  import storage from '@/common/storage'

  export default {
    data () {
      return {
        message:''
      }
    },
    methods: {
      // 
      yjfkbtn () {
        api.fankui({
          uid: this.getCookie('uid'),
          token: this.getCookie('token'),
          content:this.message
        }).then((res) => {
          console.log(res)
          if(res.status === 'ok'){
            this.promptFun({
              content: res.data
            })
          }else if(res.status === 'error'){
            this.promptFun({
              content: res.data
            })
          }
        })
      }


    },
    created () {

    },
    components: {

    }
  }
</script>

<style scoped>
  .box{
    background-color: #fff;
    padding: 30px;
    padding-bottom: 50px;
  }
  .userInfoBox{
    padding: 20px 0;
  }
  .userImg{
    width: 120px;
    height: 120px;

  }
  .userImg img{
    border-radius: 50%;
    border: 1px solid #e3e4e5;
  }
  .yjfk-box .tit2 {
    background: #F1F5F6;
    line-height: 40px;
    padding-left: 30px;
  }
  .blue {
    color: #2173c5!important;
  }
  .fk-center button {
    background: #3f8fdf;
    margin-left: 30px;
    color: #fff;
    height: 36px;
    width: 140px;
    border: 0px;
    margin-top: 60px;
  }
  .userTxt{
    padding-top: 10px;
    margin-left: 40px;
    width: 300px;
  }
  .btn_upimg{
    font-size: 16px;
    border:1px solid #e3e4e5;
    border-radius: 10px;
    width: 150px;
    height: 40px;
    margin-top: 40px;
    margin-left: 30px;
    background-color: #f1f1f1;
    position: relative;
  }
  .btn_upinfo{
    font-size: 16px;
    width: 150px;
    height: 30px;
    color: #fff;
    background-color: #3f8fdf;
  }
  .birthdayselect select {
    margin-right: 6px;
    height: 26px;
    padding: 0px 6px;
    border: 1px solid #ebebeb;
    color: #666;
  }
  .userFile{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
</style>



