import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'

// axios 
axios.defaults.baseURL = 'http://shop.100tyv.com/'
axios.defaults.withCredentials = false
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8'

// 
axios.interceptors.request.use(function (config) {
  return config;
}, function (error) {  // 
  Vue.prototype.promptFun({
    content: '',
    type: 'error'
  })
  return Promise.reject(error);
});

// 
axios.interceptors.response.use((res) => {
  if(res.data.status === 'pending') {
    Vue.prototype.goLogin() // 
  }
  return res;
},(err) => { // 
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        err.message = ''
        break

      case 401:
        err.message = ''
        break

      case 403:
        err.message = ''
        break

      case 404:
        err.message = `: ${err.response.config.url}`
        break

      case 408:
        err.message = ''
        break

      case 500:
        err.message = ''
        break

      case 501:
        err.message = ''
        break

      case 502:
        err.message = ''
        break

      case 503:
        err.message = ''
        break

      case 504:
        err.message = ''
        break

      case 505:
        err.message = 'HTTP'
        break

      default:
    }
  }
  Vue.prototype.promptFun({
    content: err.message,
    type: 'error'
  })
  return Promise.reject(err);
});

// post
export function post(url, data) {
  return new Promise((resolve, reject) => {
    axios.post(url, qs.stringify(data)) // 
    .then(res => {
      resolve(res.data);
    }, err => {
      reject(err);
    })
    .catch((error) => {
      reject(error)
    })
  })
}

// get
export function get(url){
  return new Promise((resolve,reject) => {
    axios.get(url)
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}



