<template>
  <div>
    <my-header></my-header>
    <div class="headerContent clearfix">
      <div class="box-left clearfix">
        <router-link to="/" class="logoImg">
          <img src="../../assets/images/corelogo.png" alt="">
        </router-link>
      </div>
    </div>
    <div class="box" style="background: #f1f2f6;">
      <div class="box_start ">
        <div class="f20 pl-30"></div>
      </div>
      <div class="box_between moz mt-30">
        <div class="w65  ">
          <div class="box_start mt-5">
            <div class="box_end w-100 h-40"></div>
            <div class="grid_cell h-36 box_start " v-text="OrderInfo.reason_name"></div>
          </div>
          <div class="box_start mt-5">
            <div class="box_end w-100 h-40"></div>
            <div class="grid_cell h-36 box_start ">{{OrderInfo.pay_way==='wx_pub_qr'?'':''}}</div>
          </div>
          <div class="box_start mt-5">
            <div class="box_end w-100 h-40"></div>
            <div class="grid_cell h-36 box_start " v-text="OrderInfo.refund_desc||''"></div>
          </div>
          <div class=" box_start mt-5">
            <div class="box_end w-100 h-40"></div>
            <div class="grid_cell h-36 box_start " v-text="OrderInfo.refund_no"></div>
          </div>
          <div class=" box_start mt-5">
            <div class="box_end w-100 h-40"></div>
            <div class="grid_cell h-36 box_start " v-text="OrderInfo.create_time"></div>
          </div>
          <div class=" box_start mt-5">
            <div class="box_end w-100 h-40"></div>
            <div class="grid_cell h-36 box_start f_co_fd655a" v-text="OrderInfo.refund_state==='wait_review'?'':OrderInfo.refund_state==='accept'?'':OrderInfo.refund_state==='refuse'?'':OrderInfo.refund_state==='end'?'':''"></div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="box mt-10">
      <div class="ddlist-box ddxq-spxx bg-fff" style="padding:0px 30px 30px 30px;">
        <div class="product-info"></div>
        <div class="ddlist-nav">
          <ul>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>
        <div class="table " style="margin: 10px 0px 30px;">
          <table cellspacing="0" cellpadding="0">
            <tbody><tr class="">
              <td class="td1">
                <div class="dd-l-list clearfix">
                  <ul>
                    <li class="img"><router-link :to="{path: '/mall/goodsDetails', query: {goods_id: OrderInfo.goods_id}}" ><img :src="OrderInfo.goods_img"></router-link></li>
                    <li class="txt"><a href="javascript:;" v-text="OrderInfo.goods_name"></a></li>
                  </ul>
                </div>
              </td>
              <td class="td2"><span style="text-align:center;" >&yen;<em v-text="OrderInfo.specification_price"></em></span></td>
              <td class="td2"><span style="text-align:center;" ><em v-text="OrderInfo.refund_count"></em></span></td>
              <td class="td2"><span style="text-align:center;" >&yen;<em v-text="OrderInfo.specification_price*OrderInfo.refund_count"></em></span></td>
            </tr>
            </tbody></table>
        </div>
        <div class="d-s-money" style="background:#F8F8F8">
          <ul>
            <li><em v-text="OrderInfo.refund_count"></em>&yen;<span v-text="OrderInfo.refund_actual_price"></span></li>
            <li><span class="red ">&yen;<span class="red" v-text="OrderInfo.refund_price"></span></span></li>
          </ul>
        </div>
      </div>
    </div>



    <my-footer></my-footer>
  </div>
</template>

<script>
  import MyHeader from '@/components/header/header'
  import MyFooter from '@/components/footer/footer'
  import '@/assets/css/cuimeng_style.css'
  import { Steps ,Step } from 'element-ui'
  import storage from '@/common/storage'
  import api from '@/api/order'

  export default {
    data () {
      return {
        OrderInfo:{},
        LogInfo:{}
      }
    },
    methods: {
      // 
      _getOrderInfo () {
        api.getRefundDetail({
          uid: this.getCookie('uid'),
          token: this.getCookie('token'),
          refund_id:this.$route.query.refund_id
        }).then((res) => {
          console.log(res)
          if(res.status === 'ok'){
            this.OrderInfo = res.data;
          }else if(res.status === 'error'){
            this.promptFun({
              content: res.data,
              type:"error"
            })
          }
        })
      }

    },
    created () {
      this._getOrderInfo();
    },
    mounted () {

    },
    components: {
      ElSteps: Steps,
      ElStep: Step,
      MyHeader,
      MyFooter

    }
  }
</script>

<style scoped>
  .box{
    width: 1000px;
    margin: 0 auto;
    background-color: #fff;
    padding: 30px;
  }
  .headerContent{
    width: 1200px;
    margin: 0 auto;
    padding: 20px 0;
  }
  .box-left{
    float: left;
  }
  .logoImg{
    width: 120px;
    height: 70px;
    display: block;
    float: left;
  }
  .btn {
    display: block;
    padding: 5px 15px;
    border-radius: 5px;
    color: #fff;
    margin-top: 50px;
    background-color:#3f8fdf;
    /*border: 1px solid #3f8fdf;*/
    text-align: center;
  }
  .product-info {

    height: 50px;
    line-height: 50px;
    font-size: 20px;
    color: #333333;
  }
  .ddlist-nav {
    height: 50px;
    line-height: 50px;
    background: #f1f2f6;
  }
  .ddlist-nav ul li:nth-child(1) {
    width: 405px;
  }
  .ddlist-nav ul li:nth-child(2) {
    width: 145px;
  }
  .ddlist-nav ul li:nth-child(3) {
    width: 145px;
  }
  .ddlist-nav ul li:nth-child(4) {
    width: 145px;
  }
  .ddlist-nav ul li {
    text-align: center;
    float: left;
  }
  .table table {
    border-collapse: collapse;
    width: 100%;
  }
  .ddlist-box tr {
    border: 1px solid #ebebeb;
  }
  .ddlist-box .td1 {
    width: 436px;
  }
  .ddlist-box td {
    border: 1px solid #ebebeb;
    padding: 30px 20px;
  }
  .dd-l-list:last-child {
    margin-bottom: 0px!important;
  }
  .dd-l-list:first-child {
    border: none;
  }
  .dd-l-list {
    margin-bottom: 0px;
    border-top: 1px solid #ebebeb;
  }
  .ddlist-box .td2 {
    color: #999;width: 145px;
  }
  .dd-l-list .img {
    width: 80px;
    height: 80px;
    overflow: hidden;
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
  .dd-l-list li {
    display: inline-block;
    float: left;
    line-height: 80px;
  }
  .dd-l-list .txt {
    width: 190px;
    padding-left: 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .dd-l-list .img img {
    width: 80px;
  }
  .ddlist-box .td2 span {
    display: block;
    color: #999;
  }
  .d-s-money {
    text-align: right;
    padding: 30px;
  }
  .d-s-money li {
    margin-bottom: 20px;
    color: #666;
  }
  .d-s-money span {
    display: inline-block;
    min-width: 120px;
    text-align: left;
    padding-left: 20px;
    color: #666;
  }
  .blue {
    color: #2173c5!important;
  }
  .d-s-money li:last-child {
    font-size: 24px!important;
    color: #333;
  }
  .red {
    color: #fd655a!important;
  }
  .el-step__icon{
    background: #e3e4e5!important;
  }
  .el-step__head.is-finish {
    color: #fd655a;
    border-color: #fd655a;
  }
</style>



