<template>
  <div class="pageBox">
    <!--<my-nav @one-level='_oneLevel' @two-level="_twoLevel" :type="2"></my-nav>-->
    <div class="container">
      <sort :current-page="currentPage" :all-page="allPage" @on-page="onPage" @on-sort="onSort"></sort>
      <div class="goodsListBox clearfix">
        <box-left></box-left>
        <div class="box-right">
          <div class="clearfix">
            <div class="goodsItemBox" v-for="item in goodsList" :key="item.goods_id">
              <goods-item :goods-info='item'></goods-item>
            </div>
          </div>
          <div class="paginaction" v-show="allPage > 1">
            <el-pagination
              :prev-text="''"
              :next-text="''"
              background
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="20"
              layout="total,prev, pager, next, jumper"
              :page-count="allPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Pagination } from 'element-ui'
  import storage from '@/common/storage'
  import api from '@/api/goods'
  import GoodsItem from '@/components/goodsItem/goodsItem'
  import myNav from '../private/nav'
  import sort from './private/sort'
  import boxLeft from './private/boxLeft'

  export default {
    data () {
      return {
        twoLevel: {},
        class_uuid: null,
        oneLevelName: storage.s_getItem('levelObj').paName,
        goodsList: [],
        ser_name:'',
        currentPage: 1, // 
        allPage: 1, // 
        sortType: 1, // 1()
        searchname:this.$route.query.searchname
      }
    },
    methods: {
      // 
//      _getTwoLevel (paUuid,twoUuid) {
//        api.getTwoLevel({
//          class_uuid: paUuid
//        }).then((res) => {
//          if(res.status === 'ok'){
//            this.twoLevel = res.data
//            if(!twoUuid){
//              this._getGoodsList(res.data[0].class_uuid)
//            }else {
//              this._getGoodsList(twoUuid)
//            }
//          }else if(res.status === 'error'){
//            console.log(res.data)
//          }
//        })
//      },

      // 
      _getGoodsList (name,type,p) {
        this.ser_name = name || this.searchname
        this.currentPage = p || 1
        this.sortType = type || 1
        api.getGoodsList({
          name : this.ser_name,
          type : this.sortType,
          p : this.currentPage,
          pagesize: 20
        }).then(res => {
          if(res.status === 'ok'){
            this.goodsList=[];
            this.allPage = res.data.page
            for(let i=0; i<res.data.goodsBean.length; i++){
              this.goodsList.push(res.data.goodsBean[i])
            }
          }else if(res.status === 'error'){
            this.promptFun({
              content: res.data,
              type: 'error'
            })
          }
        })
      },

      // 
      onSort (type) {
        this._getGoodsList(this.ser_name,type)
      },
      //onPage
      onPage (count) {
        this.currentPage = count
      },

//      // 
//      twoLevelClick (class_uuid) {
//        this._getGoodsList(class_uuid)
//      },
//      // nav
//      _oneLevel (item) {
//        this.oneLevelName = item.class_name
//        this._getTwoLevel(item.class_uuid)
//      },
//      // nav
//      _twoLevel (paItem,item){
//        this.oneLevelName = paItem.class_name
//        this._getTwoLevel(paItem.class_uuid, item.class_uuid)
//      },

      // 
      handleCurrentChange (val) {
        this._getGoodsList(this.ser_name, this.sortType, val)
      }
    },
    created () {
//      this.oneLevelName = storage.s_getItem('levelObj').paName
//      this._getTwoLevel(storage.s_getItem('levelObj').PaUuid,storage.s_getItem('levelObj').class_uuid)
//      this.setGoodsNav(true)
      this._getGoodsList()
    },
    components: {
      myNav,
      GoodsItem,
      sort,
      ElPagination: Pagination,
      boxLeft
    }
  }
</script>

<style scoped>
  .classPage{
    min-height: 600px;
  }
  .guideBox{
    height: 50px;
    line-height: 50px;
  }
  .guideBox a:hover{
    color: red;
  }
  .twoLevelBox{
    background-color: #fff;
    padding: 20px 20px 0 20px;
  }
  .twoTitle{
    float: left;
    font-size: 16px;
  }
  .twoLevelItemBox{
    float: left;
    font-size: 14px;
    width: 95%;
  }
  .item{
    float: left;
    padding: 0 30px 20px 30px;
    cursor: pointer;
  }
  .item:hover, .twoLevelAct{
    color: red;
  }
  .goodsListBox{
    margin: 20px 0;
  }
  .box-right{
    float: right;
    width: 960px;
  }
  .goodsItemBox{
    width: 25%;
    padding-left: 20px;
    height: 350px;
    float: left;
    margin-bottom: 20px;
  }
  .paginaction{
    margin: 10px auto 0;
    width: 80%;
  }
</style>



