/****************
* sessionStorage
* localStorage
****************/

const ss = window.sessionStorage
const ls = window.localStorage

export default {
  /*------------- sessionStorage  start -------------*/
    // 
    s_getItem(key) {
      try {
        return JSON.parse(ss.getItem(key))
      } catch (err) {
        return null
      }
    },

    // 
    s_setItem(key, val) {
      ss.setItem(key, JSON.stringify(val))
    },

    // 
    s_clear() {
      ss.clear()
    },

    s_keys() {
      return ss.keys()
    },
    
    // 
    s_removeItem(key) {
      ss.removeItem(key)
    },
  /*------------- sessionStorage  start -------------*/

  /*------------- localStorage  start -------------*/
    // 
    l_getItem(key) {
      try {
        return JSON.parse(ls.getItem(key))
      } catch (err) {
        return null
      }
    },

    // 
    l_setItem(key, val) {
      ls.setItem(key, JSON.stringify(val))
    },

    // 
    l_clear() {
      ls.clear()
    },

    l_keys() {
      return ls.keys()
    },
    
    // 
    l_removeItem(key) {
      ls.removeItem(key)
    }
    /*------------- localStorage  end -------------*/
}


