<template>
  <div class="box" >
    <dl v-show="leftType == 1">
      <dt></dt>
    </dl>
    <dl v-show="leftType == 2">
      <dt></dt>
      <dd><router-link active-class="active" :to="{path: 'help',query:{id : 6 }}"></router-link></dd>
      <dd><router-link active-class="active" :to="{path: 'help',query:{id : 7 }}"></router-link></dd>
      <dd><router-link active-class="active" :to="{path: 'help',query:{id : 8 }}"></router-link></dd>
      <dd><router-link active-class="active" :to="{path: 'help',query:{id : 9 }}"></router-link></dd>
      <dd><router-link active-class="active" :to="{path: 'help',query:{id : 10 }}"></router-link></dd>
    </dl>
    <dl v-show="leftType == 3">
      <dt></dt>
    </dl>
  </div>
</template>
<script>
  export default {
    props: ['leftType'],
    data(){
      return{
        active: false
      }
    }
  }
</script>
<style scoped>
  .router-link-active{
    color: #409EFF
  }
  a:active{
    color: #409EFF
  }
  .box{
    background-color: #fff;
    padding: 20px 20px 5px 20px;
    height: 530px;
  }
  .box dl{
    margin-bottom: 20px;
  }
  .box dl dt{
    font-size: 18px;
    padding-bottom: 15px;
    color: #409EFF;
  }
  .box dl dd{
    font-size: 14px;
    padding-bottom: 10px;
    color: #000;
  }
  .box a:hover{
    color: #3F8FDF
  }
  .tabAct{
    color: #409EFF;
  }
</style>



