<template>
  <div class="box">
    <dl v-show="leftType == 1">
      <dt></dt>
      <dd><router-link :to="{path: 'orderList'}"></router-link></dd>
      <dd><router-link :to="{path: 'refundOrder'}"></router-link></dd>
      <!--<dd><router-link :to="{path: 'evaluateList'}"></router-link></dd>-->
    </dl>
    <dl v-show="leftType == 1">
      <dt></dt>
      <dd><router-link :to="{path: 'goodsCollection'}"></router-link></dd>
      <dd><router-link :to="{path: 'myFollow'}"></router-link></dd>
    </dl>
    <dl v-show="leftType == 2">
      <dt></dt>
      <dd><router-link :to="{path: 'editUser'}"></router-link></dd>
      <dd><router-link :to="{path: 'myAddress'}"></router-link></dd>
    </dl>
  </div>
</template>
<script>
  export default {
    props: ['leftType']
  }
</script>
<style scoped>
  .router-link-active{
    color: #3F8FDF
  }
  .box{
    background-color: #fff;
    padding: 20px 20px 5px 20px;
  }
  .box dl{
    margin-bottom: 20px;
  }
  .box dl dt{
    font-size: 18px;
    padding-bottom: 15px;
  }
  .box dl dd{
    font-size: 14px;
    padding-bottom: 10px;
  }
  .box a:hover{
    color: #3F8FDF
  }
</style>



