<template>
  <div class="pageBox">
    <my-header></my-header>
    <div class="headerContent clearfix">
      <div class="box-left clearfix">
        <router-link to="/" class="logoImg">
          <img src="../../assets/images/corelogo.png" alt="">
        </router-link>
      </div>
    </div>
    <div class="inner p15">
      <h1 class="f24 box_center f_co_3F8FDF"></h1>
      <div class="mt-30 w55 moz bor p30">
        <div class="w100 box_center mb-30">
          <div class="box_end width120 mr-60 f16 f_co_333"></div>
          <input type="text" class="bor-b grid_cell" v-model="contact_name" placeholder="">
        </div>
        <div class="w100 box_center mb-30">
          <div class="box_end width120 mr-60 f16 f_co_333"></div>
          <input type="tel" maxlength="11" class="bor-b grid_cell" v-model="contact_mobile" placeholder="">
        </div>
        <div class="w100 box_center mb-30">
          <div class="box_end width120 mr-60 f16 f_co_333"></div>
          <input type="text" class="bor-b grid_cell" v-model="merchants_name" placeholder="">
        </div>
        <div class="w100 box_center mb-30">
          <div class="box_end width120 mr-60 f16 f_co_333"></div>
          <div class="itemBox grid_cell">
            <v-distpicker @province="onProvince" @city="onCity" @area="onArea" :province="address_province" :city="address_city" :area="address_country" ></v-distpicker>
          </div>
        </div>
        <div class="w100 box_center mb-30">
          <div class="box_end width120 mr-60 f16 f_co_333"></div>
          <input type="text" class="bor-b grid_cell" v-model="merchants_address" placeholder="">
        </div>
        <div class="w100 box_center mb-30">
          <div class="box_end width120 mr-60 f16 f_co_333"></div>
          <input type="text" maxlength="18" class="bor-b grid_cell" v-model="business_number" placeholder="">
        </div>
        <div>
          <h3 class="box_center"></h3>
          <div class="box_center h30 f14"></div>
          <div class="box_center mb-15">
            <div class="w140 h140 mr-10">
              <img v-show="!upimgarr0" src="../../assets/icon/ruzhushenhe_tu1@2x.png" alt="" />
              <img v-show="upimgarr0" :src="upimgarr0" alt="" />
            </div>
            <div class="p_r w140 h140">
              <img src="../../assets/icon/upId1.png" alt="">
              <form><input class="p_a l0 t0 w100 h100 opacity" type="file" @change="_upImg($event,0)" name="img" ></form>
            </div>
          </div>
          <div class="box_center mb-15">
            <div class="w140 h140 mr-10">
              <img v-show="!upimgarr1" src="../../assets/icon/ruzhushenhe_tu2@2x.png" alt="" />
              <img v-show="upimgarr1" :src="upimgarr1" alt="" />
            </div>
            <div class="p_r w140 h140">
              <img src="../../assets/icon/upId2.png" alt="">
              <form><input class="p_a l0 t0 w100 h100 opacity" type="file" @change="_upImg($event,1)" name="img" ></form>
            </div>
          </div>
          <div class="box_center mb-15">
            <div class="w140 h140 mr-10">
              <img v-show="!upimgarr2" src="../../assets/icon/ruzhushenhe_tu3@2x.png" alt="" />
              <img v-show="upimgarr2" :src="upimgarr2" alt="" />
            </div>
            <div class="p_r w140 h140">
              <img src="../../assets/icon/upId3.png" alt="">
              <form><input class="p_a l0 t0 w100 h100 opacity" type="file" @change="_upImg($event,2)" name="img" ></form>
            </div>
          </div>
          <div class="box_center mb-15">
            <div class="w140 h140 mr-10">
              <img v-show="!upimgarr3" src="../../assets/icon/ruzhushenhe_tu4@2x.png" alt="" />
              <img v-show="upimgarr3" :src="upimgarr3" alt="" />
            </div>
            <div class="p_r w140 h140">
              <img src="../../assets/icon/upId4.png" alt="">
              <form><input class="p_a l0 t0 w100 h100 opacity" type="file" @change="_upImg($event,3)" name="img" ></form>
            </div>
          </div>
          <div class="box_center h30 f14"></div>
          <div class="box_center mb-15">
            <div class="w140 h140 mr-10">
              <img v-show="upimgarr4" :src="upimgarr4" alt="" />
            </div>
            <div class="p_r w140 h140">
              <img src="../../assets/icon/upId5.jpg" alt="">
              <form><input class="p_a l0 t0 w100 h100 opacity" type="file" @change="_upImg($event,4)" name="img" ></form>
            </div>
          </div>
          <div class="main-item">
            <button class="addressBtn" @click="add_apply"></button>
          </div>
        </div>
      </div>

    </div>
    <my-footer></my-footer>
  </div>
</template>

<script>
  import MyHeader from '@/components/header/header'
  import MyFooter from '@/components/footer/footer'
  import api from '@/api/api'
  import me from '@/api/me'
  import storage from '@/common/storage'
  import VDistpicker from '@/components/VDistpicker/Distpicker'
  export default {
    data () {
      return {
        leftType: this.$route.query.apply_state,
        upimgarr:[],upimgarr0:'',upimgarr1:'',upimgarr2:'',upimgarr3:'',upimgarr4:'',
        contact_name:'',contact_mobile:'',merchants_name:'',merchants_address:'',business_number:'',
        address_province: '', address_city: '', address_country: ''
      }
    },
    methods:{
      // 
      onProvince (data) {
        this.address_province = data.value
      },
      // 
      onCity (data) {
        this.address_city = data.value
      },
      // 
      onArea (data) {
        this.address_country = data.value
      },

      // 
      _upImg (e,dx) {
        api.upImg(e)
          .then((res)=>{
            if(res.status === 'ok'){
              this.upimgarr[dx] =res.data[0] ;
              if(dx==0){
                this.upimgarr0 =this.upimgarr[0]
              }else if(dx==1){
                this.upimgarr1 =this.upimgarr[1]
              }else if(dx==2){
                this.upimgarr2 =this.upimgarr[2]
              }else if(dx==3){
                this.upimgarr3 =this.upimgarr[3]
              }else if(dx==4){
                this.upimgarr4 =this.upimgarr[4]
              }
//              console.log(this.upimgarr);
            }else if(res.status === 'error'){
              this.promptFun({
                content: res.data,
                type:'error'
              })
            }
          })
      },
      add_apply(){
        var phoneReg = /^1[3|4|5|7|8]\d{9}$/ ;
        var yyreg=/^\d{18}$/;  //18
        var payreg=/^\d{6}$/;
        if (!this.contact_name) {
          this.promptFun({content: '', type: 'error'})
          return false;
        } else if (!this.contact_mobile) {
          this.promptFun({content: '', type: 'error'})
          return false;
        } else if (!phoneReg.test(this.contact_mobile)) {
          this.promptFun({content: '', type: 'error'})
          return false;
        } else if (!this.merchants_name) {
          this.promptFun({content: '', type: 'error'})
          return false;
        } else if (!this.address_country) {
          this.promptFun({content: '', type: 'error'})
          return false;
        } else if (!this.merchants_address) {
          this.promptFun({content: '', type: 'error'})
          return false;
        }  else if (!this.business_number) {
          this.promptFun({content: '', type: 'error'})
          return false;
        } else if (!yyreg.test(this.business_number)) {
          this.promptFun({content: '18', type: 'error'})
          return false;
        } else if(!this.upimgarr4  && !(this.upimgarr0 && this.upimgarr1 && this.upimgarr2 && this.upimgarr3) ){
          this.promptFun({content: '', type: 'error'})
          return false;
        }
        api.applyseller({
          uid: this.getCookie('uid'),
          token: this.getCookie('token'),
          contact_mobile: this.contact_mobile,         // 
          contact_name: this.contact_name,             // 
          merchants_name: this.merchants_name,         // 
          merchants_address: this.merchants_address,   // 
          business_number: this.business_number,       // 
          merchants_province: this.address_province,    // 
          merchants_city: this.address_city,        // 
          merchants_country: this.address_country,     // 
          legal_img: this.upimgarr0,          // 
          legal_hand_img: this.upimgarr1,     // 
          legal_face_img: this.upimgarr2,     // 
          legal_opposite_img: this.upimgarr3,  // 
          business_img: this.upimgarr4         // 
        })
          .then((res) => {
            if(res.status === 'ok'){
              this.promptFun({
                content: '',
                type: 'success'
              })
            }else if(res.status === 'error'){
              this.promptFun({
                content: res.data,
                type: 'error'
              })
            }
          })
      }
    },
    components: {
      MyHeader,
      MyFooter,
      VDistpicker
    }
  }
</script>

<style scoped>
.inner{margin: 0 auto;width: 1200px;}.width120{width: 120px;}
.p30{padding: 30px;}.itemBox{  height: 26px;  line-height: 26px;  }
  .h30{height: 30px;}.w140{width: 140px;}.h140{height: 140px;}.mb-15{margin-bottom: 15px;}
.main-item{
  position: relative;
  margin-top: 50px;
  padding-bottom: 10px;
  zoom: 1;
  text-align: center;
  font-size: 12px;
}
.addressBtn{
  width: 100px;
  height: 30px;
  line-height: 30px;
  padding: 0 20px;
  border-radius: 5px;
  color: #fff;
  background-color: #fc6210;
  text-align: center;
  font-weight: 700;
  cursor: pointer;
}
.headerContent{
  width: 1200px;
  margin: 0 auto;
  padding: 20px 0;
}
.box-left{
  float: left;
}
.logoImg{
  width: 120px;
  height: 70px;
  display: block;
  float: left;
}
.shoppingCar{
  display: block;
  height: 38px;
  line-height: 38px;
  border: 1px solid #f1f1f1;
  font-size: 14px;
  margin: 15px 0;
  padding: 0 20px;
  float: right;
}
</style>



